const seperator = "/";
export const trimTrailingSlash = (url) => url.replace(/\/$/, "");
export const combineUrls = (base, ...parts) => trimTrailingSlash((parts || [])
    .filter(part => part)
    .reduce((url, part) => url +
    seperator +
    part
        .toString()
        .replace(/^\/+/, "")
        .replace(/\/+$/, "")
        .replace(/\/\//gm, seperator), base || ""));
