export class HttpClientError extends Error {
}
export class HttpClientParseError extends HttpClientError {
    constructor(response) {
        super();
        this.response = response;
    }
    async getResponseTextAsync() {
        return await this.response.text();
    }
}
export class HttpClientRequestFailedError extends HttpClientError {
    constructor(url, statusCode, errorResponse) {
        super(`[${url}] returned HTTP status code [${statusCode}]`);
        this.url = url;
        this.statusCode = statusCode;
        this.response = errorResponse;
    }
}
